enum TodoStatus {
  ALL = 'all',
  ACTIVE = 'active',
  COMPLETED = 'completed',
}

type Props = {
  setSortType: (status: TodoStatus) => void,
};

export const TodoFilter: React.FC<Props> = ({ setSortType }) => {
  const handler = (status: TodoStatus) => {
    setSortType(status);
  };

  return (
    <form className="field has-addons">
      <p className="control">
        <span className="select">
          <select data-cy="statusSelect">
            <option
              value="all"
              onChange={() => handler(TodoStatus.ALL)}
            >
              All
            </option>
            <option
              value="active"
              onChange={() => handler(TodoStatus.ACTIVE)}
            >
              Active
            </option>
            <option
              value="completed"
              onChange={() => handler(TodoStatus.COMPLETED)}
            >
              Completed
            </option>
          </select>
        </span>
      </p>

      <p className="control is-expanded has-icons-left has-icons-right">
        <input
          data-cy="searchInput"
          type="text"
          className="input"
          placeholder="Search..."
        />
        <span className="icon is-left">
          <i className="fas fa-magnifying-glass" />
        </span>

        <span className="icon is-right" style={{ pointerEvents: 'all' }}>
          {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
          <button
            data-cy="clearSearchButton"
            type="button"
            className="delete"
          />
        </span>
      </p>
    </form>
  );
};
